body {
	max-height: 100vh;
	max-width: 100vw;
	width: 100vw;
	overflow: hidden;
}

* {
	font-family: 'Roboto', sans-serif;
}

.App {
	margin: 0;
	padding: 0;
	width: 100vw;
	text-align: center;
}

.App-logo {
	height: 40vmin;
}


.header {
	background: #080e0e;
	height: 5vh;
	width: 100%;
	border: 1px solid;
}

.main {
	background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, #0b1313), color-stop(75%, #000000));
	background: linear-gradient(#0b1313 25%, #000000 75%);
	height: 85vh;
	width: 100%;
	width: 100vw;
}

.btn-primary {
	background: black;
	padding: 1% 5% 1% 5%;
	color: white;
	font-size: 1.3em;
}

.search-input {
	width: 50%;
	background: rgba(0, 0, 0, 0.6);
	border: 1px solid transparent;
	border-top: 1px solid green;
	border-bottom: 1px solid green;
	padding: 1em;
	color: rgba(255, 255, 255, 0.4);
	-webkit-transition: 1.3s all;
	transition: 1.3s all;
}

.album-link {
	text-decoration: none;
}

.album-link:hover {
	text-shadow: 1px 1px rgba(255, 255, 255, .3);
}

.album-link:focus {
	text-decoration: none;
	border: 0px solid transparent;
	outline: 0px solid transparent;
}

.album-link:active {
	-webkit-transform: scale(1.02);
	        transform: scale(1.02);
	letter-spacing: 4px;
}

.search-input:focus {
	width: 55%;
	background: rgba(0, 0, 0, 0.6);
	border-top: 1px solid green;
	-webkit-transition: 1.3 all;
	transition: 1.3 all;
	color: rgba(255, 255, 255, 1);
}

.input-holder {
	width: 100%;
	padding-top: 1%;
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.search-icon {
	border: 1px solid green;
	border-right: 0px solid transparent;
	padding-left: 1%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: rgba(255, 255, 255, 0.4);
	background: rgba(0, 0, 0, 0.6);
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
	cursor: pointer;
	margin-right: -0.2%;
}

.cancel-icon {
	cursor: pointer;
	border: 1px solid green;
	border-left: 0px solid transparent;
	cursor: pointer;
	margin-left: -0.1%;
	padding-right: 1%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: rgba(255, 255, 255, 0.4);
	background: rgba(0, 0, 0, 0.6);
	border-radius: 0px;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
}

.search-body {
	padding-top: 2%;
