	background-repeat: no-repeat;
	background-position: center center;
	max-width: 100%;
	max-height: 100%;
	margin-bottom: 5%;
	border-radius: 50%;
}

.artist-block:hover .artist-img {
	-webkit-filter: hue-rotate(270deg);
	        filter: hue-rotate(270deg);
