	transition: all 5s;
}

.artist-block .artist-hover-state {
	opacity: 0;
	-webkit-transition: all 0.6s;
	transition: all 0.6s;
}

.artist-block:hover .artist-hover-state {
	opacity: 1;
}

.artist-icon-holder {
